@font-face {
  font-family: "Oswald";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Oswald-Regular.woff");
  src: url("../fonts/Oswald-Regular.woff2");
}
@font-face {
  font-family: "Oswald";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Oswald-Bold.woff");
  src: url("../fonts/Oswald-Bold.woff2");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  height: 100%;
  font-size: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  line-height: 27px;
}
.page__body {
  min-height: 100%;
  margin: 0;
  color: #000000;
  background-color: #ffffff;
}
